/* CANVAS CSS */

header {
  height: 30px;
  text-align: center;
  font-family: 'VT323', monospace;
  font-size: 32px;
}
#game-container {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  z-index: 0;
  background-image: url(/static/media/space.70311e33.gif);
}
#hud-canvas {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  z-index: 3;
}
#game-canvas {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  z-index: 2;
}
#background-canvas {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  z-index: 1;
}
/* UI CSS */

.box {
  padding-bottom: 20px;
  margin: 20px auto;
  background: black;
  width: 600px;
  border-radius: 10px;
  border: 5px solid #fc03a1;
  opacity: .9;
}

.mainpage-container {
  height: 100vh;
  width: 100vw;
  text-align: center;
  position: absolute;
  /* background-image: url('../images/space.png'); */
  color: #ffe600;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  overflow: scroll;
}



@keyframes move-twink-back {
    from {background-position:0 0;}
    to {background-position:-10000px 5000px;}
}
@-webkit-keyframes move-twink-back {
    from {background-position:0 0;}
    to {background-position:-10000px 5000px;}
}

@keyframes move-clouds-back {
    from {background-position:0 0;}
    to {background-position:10000px 0;}
}
@-webkit-keyframes move-clouds-back {
    from {background-position:0 0;}
    to {background-position:10000px 0;}
}

.stars, .twinkling, .clouds {
  position:absolute;
  top:0;
  left:0;
  right:0;
  bottom:0;
  width:100%;
  height:100%;
  display:block;
}

.stars {
  background:#000 url(/static/media/stars.6494ab5b.png) repeat top center;
  z-index:-3;
}

.twinkling{
  background:transparent url(data:image/png;base64,) repeat top center;
  z-index:-2;
  -webkit-animation:move-twink-back 200s linear infinite;
  animation:move-twink-back 200s linear infinite;
}

.clouds{
    background:transparent url(/static/media/clouds.6af48925.png) repeat top center;
    z-index:-1;
  -webkit-animation:move-clouds-back 200s linear infinite;
  animation:move-clouds-back 200s linear infinite;
}
/* NavBar CSS */

.navbar {
  display: flex;
  justify-content: space-between;
  height: 45px;
  padding: 20px 20px 0px 20px;
  z-index: 1;
}

.nav{
  display: flex;
}

.nav-select {
  padding-right: 30px;
  text-align: center;
  font-size: 20px;
  color: #ffe600;
}

.nav-select:hover {
  color: #fc03a1;
  cursor: pointer;
}

.active {
  color: #fc03a1;
  fill: #fc03a1;
}

/* Signup & Login CSS */

.login-container {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: black;
  width: 400px;
  border-radius: 10px;
  border: 5px solid #fc03a1;
  opacity: .9;
}

.form-container-login, .form-container-signup{
  position: absolute;
  width: 350px;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: black;
  padding: 40px;
  border-radius: 10px;
  border: 5px solid #fc03a1;
  opacity: .9;
  color: #fc03a1;
  padding-bottom: 20px;
}

