  display: none;
} */

.input-field {
  font-weight: 200;
  height: 50px;
  font-size: 16px;
  width: 225px;
  padding-left: 20px;
  margin-bottom: 10px;
  border-radius: 10px;
  border: 5px solid #fc03a1;
  background-color: #ffe600;
  color: black;
  -webkit-transition: ease-in-out .4s;
  transition: ease-in-out .4s;
}

.input-field::-webkit-input-placeholder {
  color: #fc03a1;
}

.input-field:-ms-input-placeholder {
  color: #fc03a1;
}

.input-field::-ms-input-placeholder {
  color: #fc03a1;
}

.input-field::placeholder {
  color: #fc03a1;
}

.input-field:focus {
  -webkit-transform:scale(1.05);
          transform:scale(1.05);
}

.submit-button, .demo-login-button {
  color: black;
  height: 50px;
  font-size: 16px;
  width: 225px;
  padding-left: 0px;
  margin-bottom: 10px;
  border-radius: 10px;
  background-color: #fc03a1;
  -webkit-transition: ease-in-out .4s;
  transition: ease-in-out .4s;
  cursor: pointer;
}

.submit-button:hover, .demo-login-button:hover, .join-button:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.alt-text {
  text-align: center;
  padding-top: 10px;
  font-size: 14px;
  color: #fc03a1;
}

.errors {
  font-size: 14px;
  color: #ffe600
}

.logout-button, .login-button {
  background-color: transparent ;
  font-weight: 200;
  color: #fff;
  height: 35px;
  width: 80px;
  font-size: 14px;
  border-radius: 10px;
  border: 2px solid #fc03a1;
  cursor: pointer;
  margin-top: -8px;
}

.logout-button:hover, .login-button:hover {
  background-color: #ffe600;
  color: #000;
}

.pointer {
  cursor: pointer;
}

.title {
  text-align: center;
  font-size: 20px;
  padding-bottom: 20px;
  color: #ffe600;
}

.home-button {
  height: 30px;
  margin-top: -7px;
  fill: #ffe600;
}

.home-button:hover {
  fill: #fc03a1;
}

.greeting {
  padding-top: 3px;
