.instructions {
  line-height: 1.3;
  padding-top: 5px;
}

.lobby-content {
  width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.play {
  height: 70px;
  width: 200px;
  margin: auto;
  border-radius: 10px;
  border: 5px solid #fc03a1;
  background-color: #ffe600;
  color: #fc03a1;
  font-size: 30px;
  text-align: center;
  padding-top: 15px;
  -webkit-transition: ease-in-out .4s;
  transition: ease-in-out .4s;
  cursor: pointer;
}

.play:hover {
  -webkit-transform:scale(1.05);
          transform:scale(1.05);
}

.server-list {
  display: grid;
  grid-template-columns: 50% 50%;
}

.server-column {
  min-width: 300px;
  text-align: center;
