/* Options CSS */

.options-container {
  display: grid;
  grid-template-columns: 60% 40%;
  position: absolute;
  width: 90%;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.customize-panel {
  width: 90%;
  margin: 20px auto;
  border-radius: 10px;
  border: 5px solid #fc03a1;
  background-color: #ffe600;
  color: #fc03a1;
  opacity: .9;
}

.customize-title{
  font-size: 30px;
  text-align: center;
  padding-top: 20px;
}

.currency {
  font-size: 28px;
  text-align: center;
  color: black;
  padding-top: 30px;
}

.select-customization{
  display: flex;
  background: black;
  font-size: 22px;
  margin-top: 20px;
}

.customize.active {
  background: #fc03a1;
  color: black;
}

.customize {
  width: 50%;
  text-align: center;
  padding: 5px 0px 5px 0px;
  cursor: pointer;
}

.picture-container {
  text-align: center;
  margin-top: 40px;
}

.purchase-container {
  padding-top: 40px;
}

.ship, .ball {
