  font-size: 14px;
  color: #ffe600;
  cursor: pointer;
}

.ship-options, .ball-options {
  padding-top: 30px;
  padding-bottom: 56px;
  /* border-bottom: 2px solid; */
  display: flex;
  justify-content: space-evenly;
}

.ball-options {
  border-bottom: none;
}

.select-button {
  background: black;
  border-radius: 5px;
  border: 2px solid #fc03a1;
  padding: 10px;
  width: 20%;
  text-align: center;
  cursor: pointer;
  color: #ffe600;
}

.select-button.active {
  background: #fc03a1;
  color: black;
  border: 2px solid black;
}

.select-button:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.ship-selected-message, .ball-selected-message {
  color: black;
  text-align: center;
  font-size: 18px;
  background: #fc03a1;
