  padding: 20px;
  letter-spacing: 5px;
  font-weight: 700;
  cursor: default;
}

.select-ship, .select-ball {
  background: black;
  color: #ffe600;
  text-align: center;
  font-size: 18px;
  border-top: 5px solid #fc03a1;
  padding: 20px;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
