}

.sell-button {
  width: 150px;
  border: 2px solid;
  border-radius: 5px;
  margin: auto;
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 9px 0px 7px 0px;
  -webkit-transition: ease-in-out .4s;
  transition: ease-in-out .4s;
  cursor: pointer;
}

.sell-button:hover {
  background: #ffe600;
  border: 2px solid #fc03a1;
