}

.default-button {
  width: 150px;
  color: #fc03a1;
  background: #ffe600;
  border: 2px solid #fc03a1;
  border-radius: 5px;
  margin: auto;
  padding: 9px 0px 7px 0px;
  margin-top: 20px;
  margin-bottom: 20px;
}

.buy-button {
  width: 100px;
  color: #fc03a1;
  background: #ffe600;
  border: 2px solid #fc03a1;
  border-radius: 5px;
  margin: auto;
  padding: 9px 0px 7px 0px;
  margin-top: 20px;
  margin-bottom: 20px;
